header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

/*============= CTA ===============*/

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/*===================== Socail ================*/

.header_social {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_social::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}
.header_social:hover{
  font-size: 1.4rem;
  }

/*=========== IMG ============*/
.img {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
  justify-content: center;
}

/*======================= Scroll DOwn ============*/

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}
.scroll_down:hover{
  font-weight: 500;
  transition: var(--transition);
}


/*============== Media Queries (Medium Devices)===============*/

@media screen and (max-width: 1024px) {
    header{
        height: 68vh;

    }
  }
  /*============== Media Queries (Small Devices)===============*/
  
  @media screen and (max-width: 600px) {
    header{
        height: 140vh;
    }

    .header_social, .scroll_down{
        display: none;
    }
  }
  